import PropTypes from 'prop-types';
// import { useState } from 'react';
// import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import useMediaQuery from '@mui/material/useMediaQuery';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';

export default function CollageTypeMenu({
  collageType,
  width,
  sizes,
  handleTypeChange,
}) {
  const belowsm = useMediaQuery((theme) => theme.breakpoints.down('sm'));
  // const [collageType, setCollageType] = useState('A4');
  const handleChange = (event) => {
    const ct = event.target.value;
    handleTypeChange(ct);
    // setCollageType(ct);
  };
  return (
    <Stack
      direction="row"
      alignItems="center"
      justifyContent="center"
      sx={{
        width: belowsm ? '100%' : width,
        textAlign: 'middle',
      }}
    >
      <Typography
        sx={{
          color: 'form.input',
          fontSize: '0.87rem',
          fontWeight: 'bold',
        }}
      >
        Collage Type:{' '}
      </Typography>
      <FormControl
        variant="standard"
        size="small"
        margin="none"
        hiddenLabel
        sx={{ mx: 1, alignItems: 'center' }}
      >
        <Select
          id="demo-simple-select-standard"
          value={collageType}
          onChange={handleChange}
          label="Age"
          disableUnderline
          inputProps={{
            sx: {
              p: 0,
              fontWeight: 'bold',
              fontSize: '0.82rem',
              color: 'primary.main',
              pl: 1,
            },
          }}
        >
          {Object.keys(sizes).map((key) => (
            <MenuItem key={sizes[key].text} value={key}>
              {sizes[key].text}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </Stack>
  );
}

CollageTypeMenu.propTypes = {
  collageType: PropTypes.string.isRequired,
  width: PropTypes.number.isRequired,
  sizes: PropTypes.objectOf(
    PropTypes.shape({
      width: PropTypes.number.isRequired,
      height: PropTypes.number.isRequired,
      text: PropTypes.string.isRequired,
    })
  ).isRequired,
  handleTypeChange: PropTypes.func.isRequired,
};
