import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
import useBase from 'hooks/collage/base';
import { useNavigate } from 'react-router-dom';
import Dialog from '@mui/material/Dialog';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
import DialogContent from '@mui/material/DialogContent';
import Box from '@mui/material/Box';

import Collage from 'components/collage/Collage';
import DownloadDialog from 'components/collage/DownloadDialog';
import SaveDialog from 'components/collage/SaveDialog';
import SaveStatusDialog from 'components/collage/SaveStatusDialog';
import Header from 'components/collage/Header';
import Loading from 'components/Loading';
import View from 'components/collage/View';

import DesktopActions from 'components/collage/DesktopActions';
import DesktopControls from 'components/collage/DesktopControls';
import DesktopImageOptions from 'components/collage/DesktopImageOptions';
import EditControl from 'components/collage/EditControl';
import MobileActions from 'components/collage/MobileActions';
import MobileControls from 'components/collage/MobileControls';
import CollageTypeMenu from 'components/collage/CollageType';

export default function BaseCollage({ getCollage, clearCollage, saveType }) {
  console.log('base collage', saveType);
  const navigate = useNavigate();
  const [details, setDetails] = useState({});
  const [status, setStatus] = useState('fetching');
  const [commonOpen, setCommonOpen] = useState(false);
  const [commonMessage, setCommonMessage] = useState('');

  const openCommonDialog = (message) => {
    console.log('opening common dialog with message', message);
    if (message !== '') {
      setCommonMessage(message);
      setCommonOpen(true);
    }
  };

  const closeCommonDialog = () => {
    console.log('closing common dialog', commonMessage);
    setCommonOpen(false);
    setCommonMessage('');
  };
  console.log('details', details);

  const [
    collageType,
    getDetails,
    collageName,
    ,
    collageID,
    constants,
    dimensionProps,
    optionProps,
    actionProps,
    imageProps,
    treeProps,
    handleClear,
    collageCallback,
    handleRemoveImage,
    newCollage,
    saveStatus,
    saveCollage,
    handleCollageTypeChange,
    handleNameChange,
  ] = useBase(details);

  useEffect(async () => {
    if (Object.keys(details).length === 0) {
      console.log('savetype', saveType, Object.keys(details).length);
      if (saveType === 'existing') {
        const collageDetails = await getCollage(
          imageProps.setImageCount,
          imageProps.incrementNewImageCount
        );
        setDetails(collageDetails);
      }
      console.log('status calculation fetch end', imageProps.imgCount);
      setStatus('fetch end');
      // if (saveType === 'new') {
      //   clearCollage();
      // }
    }
  }, []);

  console.log(
    'base collage',
    details,
    imageProps.imgCount,
    imageProps.loaded,
    imageProps.images,
    imageProps.newImagesLoaded,
    collageName,
    collageID
  );

  useEffect(() => {
    let st = status;
    // if (st === 'fetching') return;
    console.log(
      'status calculation vars',
      saveType,
      st,
      imageProps.imgCount,
      Object.keys(details).length
    );
    if (st === 'fetching' && saveType === 'new') st = 'empty';
    if (imageProps.imgCount === 0 && st === 'ready') {
      st = 'empty';
    }
    if (
      imageProps.imgCount === 0 &&
      Object.keys(details).length === 0 &&
      st === 'fetch end'
    ) {
      console.log('status calculation setting emmmpty');
      st = 'empty';
    }
    if (
      imageProps.images.length > 0 &&
      imageProps.images.length === imageProps.imgCount
    ) {
      st = 'preparing';
    }
    if (imageProps.images.length < imageProps.imgCount) {
      st = 'loading';
    }
    if (imageProps.loaded >= imageProps.imgCount && imageProps.imgCount > 0) {
      st = 'ready';
    }
    setStatus(st);
    console.log(
      'status calculation',
      imageProps.loaded,
      imageProps.imgCount,
      imageProps.images.length,
      st,
      details
    );
  }, [imageProps]);

  const clearAll = async () => {
    openCommonDialog('Deleting Collage');
    setDetails({});
    handleClear();
    await clearCollage();
    setStatus('fetch end');
    closeCommonDialog();
    if (saveType === 'existing') {
      openCommonDialog('Navigating to collages page');
      setTimeout(() => {
        closeCommonDialog();
        navigate('/collages');
      }, 3000);
    }
  };

  const handleSave = async (name) => {
    console.log('in handle save', getDetails(name), imageProps.images);

    actionProps.onSaveClick();
    const res =
      saveType === 'new' ? await newCollage(name) : await saveCollage(name);
    console.log('save results', res, collageID);
    if (res) {
      setTimeout(() => {
        if (saveType === 'new') clearAll();
        actionProps.setSaveStart(false);
        navigate('/collages');
      }, 3000);
    }
  };

  const downloadDialog = (
    <DownloadDialog
      open={actionProps.open}
      onClick={actionProps.onDownloadClick}
      onClose={actionProps.onDownloadClose}
      sizes={constants.paperSizes}
    />
  );

  const saveDialog = (
    <SaveDialog
      collageName={collageName}
      open={actionProps.saveOpen}
      onClick={handleSave}
      onClose={actionProps.onSaveClose}
    />
  );

  const saveStatusDialog = (
    <SaveStatusDialog open={actionProps.saveStart} status={saveStatus} />
  );

  const collage = (
    <Collage
      dimensionProps={dimensionProps}
      imageProps={imageProps}
      actionProps={actionProps}
      collageCallback={collageCallback}
      handleRemoveImage={handleRemoveImage}
      options={optionProps.options}
      tree={treeProps.tree}
    />
  );

  const mobileControls = (
    <MobileControls
      optionProps={optionProps}
      actionProps={actionProps}
      dimensionProps={dimensionProps}
      handleClear={clearAll}
      saveType={saveType}
    />
  );

  const header = (
    <Header
      headerRef={dimensionProps.headerRef}
      name={collageName}
      handleNameChange={handleNameChange}
    />
  );

  const desktopPhotosComp = (
    <DesktopImageOptions
      onDelete={actionProps.onDelete}
      onAdd={dimensionProps.handleImageAddClick}
      clearDelete={actionProps.clearDelete}
    />
  );
  const desktopControls = (
    <DesktopControls
      innerRef={dimensionProps.footerRef}
      onRefresh={actionProps.onRefresh}
      photosComp={desktopPhotosComp}
      editComp={<EditControl optionProps={optionProps} />}
      handleClear={clearAll}
      clearDelete={actionProps.clearDelete}
      saveType={saveType}
    />
  );

  const isMobile = window.innerWidth < window.innerHeight;
  const controls = isMobile ? mobileControls : desktopControls;

  const onSave = async () => actionProps.handleSave();

  const actionsComp = isMobile ? (
    <MobileActions
      actionsRef={dimensionProps.actionsRef}
      onDownload={actionProps.handleDownload}
      onSave={onSave}
    />
  ) : (
    <DesktopActions
      actionsRef={dimensionProps.actionsRef}
      onDownload={actionProps.handleDownload}
      onSave={onSave}
    />
  );

  const collageTypeMenu = (
    <CollageTypeMenu
      collageType={collageType}
      width={dimensionProps.width}
      sizes={constants.paperSizes}
      handleTypeChange={handleCollageTypeChange}
    />
  );

  return (
    <Box sx={{ backgroundColor: 'gray', height: '100vh' }}>
      <View
        open={actionProps.open}
        saveOpen={actionProps.saveOpen}
        saveStatusOpen={actionProps.saveStart}
        dimensionProps={dimensionProps}
        imageProps={imageProps}
        header={header}
        actions={actionsComp}
        downloadDialog={downloadDialog}
        saveDialog={saveDialog}
        saveStatusDialog={saveStatusDialog}
        collage={collage}
        controls={controls}
        status={status}
        saveType={saveType}
        collageTypeMenu={collageTypeMenu}
      />
      <input
        ref={dimensionProps.addImageRef}
        type="file"
        hidden
        multiple
        accept="image/*"
        onChange={imageProps.handleLoad}
      />
      {actionProps.generate ? <Loading title="Creating collage ..." /> : null}
      <Dialog open={commonOpen}>
        <DialogContent>
          <Stack
            direction="row"
            justifyContent="space-between"
            alignItems="center"
            spacing={2}
          >
            <CircularProgress />
            <Typography variant="h6">{commonMessage}</Typography>
          </Stack>
        </DialogContent>
      </Dialog>
    </Box>
  );
  // return 'base';
}

BaseCollage.defaultProps = {
  getCollage: () => {},
  clearCollage: () => {},
};

BaseCollage.propTypes = {
  getCollage: PropTypes.func,
  clearCollage: PropTypes.func,
  saveType: PropTypes.string.isRequired,
};
