import PropTypes from 'prop-types';
import { useState } from 'react';
// import { alpha } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import { Typography, Box, Link } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ProfileLink from 'components/profile';
import { Branding, BrandLogo } from 'components/brand';

function HeaderNameSection({ name, handleChange }) {
  const [nameError] = useState('');
  const [editable, setEditable] = useState(false);
  const handleClick = () => setEditable(true);
  const handleBlur = (e) => {
    if (e.target.value === '') {
      handleChange('Untitled');
    } else {
      handleChange(e.target.value);
    }
    setEditable(false);
  };

  const handleKeyDown = (e) => {
    if (e.keyCode === 13 && e.shiftKey === false) {
      handleBlur(e);
    }
  };

  return editable === true ? (
    <TextField
      variant="filled"
      label="Set Collage Name"
      defaultValue={name}
      autoFocus
      size="small"
      onBlur={handleBlur}
      onKeyDown={handleKeyDown}
      InputLabelProps={{
        color: 'secondary',
        focused: true,
      }}
      InputProps={{
        sx: {
          color: 'white',
        },
      }}
      helperText={nameError}
      error={!!nameError}
      sx={{
        position: 'absolute',
        left: '50%',
        transform: 'translate(-50%, 0)',
        cursor: 'text',
      }}
    />
  ) : (
    <Typography
      variant="h4"
      component="h4"
      onClick={handleClick}
      sx={{
        color: 'gray',
        position: 'absolute',
        left: '50%',
        transform: 'translate(-50%, 0)',
      }}
    >
      {name}
    </Typography>
  );
}

HeaderNameSection.propTypes = {
  name: PropTypes.string.isRequired,
  handleChange: PropTypes.func.isRequired,
};

export default function Header({ headerRef, name, handleNameChange }) {
  console.log('in collage header', name);
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  return (
    <Paper
      ref={headerRef}
      sx={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        p: 1,
        backgroundColor: 'primary.main',
        borderRadius: 0,
      }}
      elevation={0}
    >
      <Box sx={{ postion: 'absolute', left: '5px' }}>
        <Stack direction="row" alignItems="center" spacing={2}>
          <Link href="/collages" color="white" underline="none">
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <ArrowBackIcon fontSize="medium" />
            </Box>
          </Link>
          {belowmd ? (
            <BrandLogo width="50px" height="50px" />
          ) : (
            <Branding width="50px" height="50px" />
          )}
        </Stack>
      </Box>
      <HeaderNameSection name={name} handleChange={handleNameChange} />
      <ProfileLink />
    </Paper>
  );
}

Header.propTypes = {
  name: PropTypes.string.isRequired,
  headerRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]).isRequired,
  handleNameChange: PropTypes.func.isRequired,
};
