import { useRef, useState, useEffect } from 'react';

export default function useDimensions(constants) {
  const { fontSize, lineHeight } = constants;
  const headerRef = useRef();
  const actionsRef = useRef();
  const footerRef = useRef();
  const addImageRef = useRef();

  const [height, setHeight] = useState(0);
  const [imageHeight, setImageHeight] = useState(0);
  const [imageWidth, setImageWidth] = useState(0);
  const [width, setWidth] = useState(0);
  const [maxHeight, setMaxHeight] = useState(0);
  const [maxWidth, setMaxWidth] = useState(0);
  const [textHeight, setTextHeight] = useState(0);

  const setDimensions = () => {
    const isMobile = window.innerWidth < window.innerHeight;
    const headerHeight = headerRef.current ? headerRef.current.offsetHeight : 0;
    if (isMobile === true) {
      const actionsHeight = actionsRef.current
        ? actionsRef.current.offsetHeight
        : 0;
      const footerHeight = footerRef.current
        ? footerRef.current.offsetHeight
        : 0;
      const otherHeight = headerHeight + actionsHeight + footerHeight;
      setMaxWidth(window.innerWidth);
      setMaxHeight(window.innerHeight - otherHeight);
    } else {
      const actionsWidth = actionsRef.current
        ? actionsRef.current.offsetWidth
        : 0;
      const footerWidth = footerRef.current ? footerRef.current.offsetWidth : 0;
      setMaxWidth(window.innerWidth - (footerWidth + actionsWidth));
      setMaxHeight(window.innerHeight - headerHeight);
    }
  };

  const handleImageAddClick = () => {
    if (addImageRef.current) addImageRef.current.click();
  };

  useEffect(() => {
    setDimensions();
  }, [headerRef.current, actionsRef.current, footerRef.current]);

  useEffect(() => {
    setDimensions();

    window.addEventListener('resize', setDimensions);

    return () => window.removeEventListener('resize', setDimensions);
  }, []);

  return {
    addImageRef,
    actionsRef,
    headerRef,
    footerRef,
    width,
    height,
    imageHeight,
    imageWidth,
    setImageWidth,
    setImageHeight,
    maxHeight,
    maxWidth,
    handleImageAddClick,
    textHeight,
    setTextHeight,
    fontSize,
    lineHeight,
    setWidth,
    setHeight,
  };
}
