import { useEffect, useState, useLayoutEffect } from 'react';
import useDimensions from 'hooks/collage/dimensions';
import useOptions from 'hooks/collage/options';
import useActions from 'hooks/collage/actions';
import useImages from 'hooks/collage/images';
import useTree from 'hooks/collage/tree';

import helpers from 'components/collage/helpers';
import collageapi from 'api/collage';

export default function useBase(initialValue) {
  console.log('use base', initialValue);
  const [details, setDetails] = useState(initialValue);
  const constants = {
    defaultCollageName: 'Untitled',
    defaultCollageType: 'A4',
    tolerance: 0.1,
    resizeImageWidth: 1500,
    fontSize: 20,
    lineHeight: 25,
    thumbnailHeight: 500,
    paperSizes: {
      A4: { width: 2480, height: 3508, text: 'A4 Potrait' },
      A3: { width: 3508, height: 4961, text: 'A3 Potrait' },
      A4LandScape: { height: 2480, width: 3508, text: 'A4 LandScape' },
      A3LandScape: { height: 3508, width: 4961, text: 'A3 LandScape' },
      FBCoverDesktop: { height: 740, width: 2000, text: 'FB Cover Desktop' },
      WhatsappStatus: { height: 1920, width: 1080, text: 'Whatsapp Status' },
    },
  };

  const [collageName, setCollageName] = useState(constants.defaultCollageName);
  const [collageType, setCollageType] = useState(constants.defaultCollageType);

  const handleCollageTypeChange = (ct) => setCollageType(ct);
  const getAspectRatio = () =>
    constants.paperSizes[collageType].width /
    constants.paperSizes[collageType].height;

  const handleNameChange = (newName) => setCollageName(newName);

  const [uploadedImages, setUploadedImages] = useState(0);
  const dimensions = useDimensions(constants);
  const options = useOptions();
  const actions = useActions();
  const images = useImages(details);

  const treeState = useTree(details, options.setOptions);
  const [thumbnail, setThumbnail] = useState({});
  const collageID =
    'details' in initialValue && 'collage_id' in initialValue.details
      ? initialValue.details.collage_id
      : Date.now();

  useLayoutEffect(() => {
    setDetails(initialValue);
    if ('details' in initialValue) {
      if (
        'name' in initialValue.details &&
        collageName === constants.defaultCollageName
      ) {
        console.log('setting collage name', initialValue.details.name);
        setCollageName(initialValue.details.name);
      }
      if (
        'collage_type' in initialValue.details &&
        collageType === constants.defaultCollageType
      ) {
        console.log('setting collage name', initialValue.details.collage_type);
        setCollageType(initialValue.details.collage_type);
      }
    }
  }, [initialValue]);

  const initialStatus = () => ({
    details: {
      text: 'Saving Details',
      status: 'pending',
    },
    images: {
      text: `Saving Images (${uploadedImages} / ${images.images.length})`,
      status: 'pending',
    },
    finalizing: {
      text: 'Finalizing Collage',
      status: 'pending',
    },
  });
  const [saveStatus, setSaveStatus] = useState(initialStatus);

  const updateStatus = (key, status) => {
    const value = { ...saveStatus[key], status };
    setSaveStatus((st) => ({ ...st, [key]: value }));
  };

  const updateImagesStatus = () => {
    const imagesText = `Saving Images (${uploadedImages} / ${images.imgCount})`;
    let value = { ...saveStatus.images, text: imagesText };
    if (uploadedImages === images.imgCount && images.imgCount > 0) {
      value = { ...value, status: 'done' };
    }
    setSaveStatus((st) => ({ ...st, images: value }));
  };

  useEffect(() => {
    console.log('useeffect 3');
    updateImagesStatus();
  }, [uploadedImages, images.imgCount]);

  const handleClear = async () => {
    const clearPromises = await Promise.allSettled([
      treeState.clear(),
      images.clear(),
      options.clear(),
    ]);

    for (let i = 0; i < clearPromises.length; i += 1) {
      if (
        clearPromises[i].status === 'fulfilled' ||
        clearPromises[i].value.ok === true
      ) {
        return true;
      }
    }
    return false;
  };

  useEffect(() => {
    console.log('aspect ratio changed', getAspectRatio());
    const ar = getAspectRatio();
    console.log('useeffect tree', treeState.tree);
    if (
      Object.keys(treeState.tree).length > 0 &&
      'aspectRatio' in treeState.tree.root.value &&
      treeState.tree.root.value.aspectRatio !== ar
    ) {
      console.log(
        'useeffect vals',
        treeState.tree,
        ar,
        treeState.tree.root.value.aspectRatio
      );
      const newAspectRatioTree = treeState.tree.copy();
      newAspectRatioTree.value = {
        ...newAspectRatioTree.value,
        aspectRatio: ar,
      };
      newAspectRatioTree.root.value = {
        ...newAspectRatioTree.root.value,
        targetAspectRatio: ar,
      };
      treeState.setTree(newAspectRatioTree);
      if (images.images.length > 1) actions.setRefresh(true);
    }
  }, [collageType]);

  useEffect(() => {
    const ar = getAspectRatio();
    const { maxWidth, maxHeight } = dimensions;
    const [w, h] = helpers.getCollageDimension(
      maxWidth - 10,
      maxHeight - 30,
      ar
    );
    dimensions.setWidth(w);
    dimensions.setHeight(h);
  }, [dimensions.maxWidth, dimensions.maxHeight, collageType]);

  useEffect(() => {
    console.log('useeffect 2');
    if (
      (images.newImgCount > 0 &&
        images.newImgCount === images.newImagesLoaded) ||
      actions.refresh
    ) {
      images.setNewImageCount(0);
      images.setNewImagesLoaded(0);
      treeState.reAdjust(
        images.images,
        constants.tolerance,
        dimensions.width,
        dimensions.height - dimensions.textHeight,
        options.options
      );
      actions.setRefresh(false);
    }
  }, [images.newImgCount, images.newImagesLoaded, actions.refresh]);

  const collageCallback = () => {};

  const handleRemoveImage = (imgTitle) => {
    const newImagesArr = images.remove(imgTitle);
    treeState.reAdjust(
      newImagesArr,
      constants.tolerance,
      dimensions.imageWidth,
      dimensions.imageHeight,
      options.options
    );
  };

  const generateThumbnail = () => {
    const ar = treeState.tree.root.value.aspectRatio;
    const thumbnailWidth = constants.thumbnailHeight * ar;
    const thumbnailCanvas = document.createElement('canvas');
    const thumbnailTree = treeState.tree.copy();
    thumbnailTree.root.options.text = '';
    thumbnailTree.addSize(thumbnailWidth, constants.thumbnailHeight, ar);
    thumbnailCanvas.width = thumbnailWidth;
    thumbnailCanvas.height = constants.thumbnailHeight;
    thumbnailTree.CanvasDisplay(
      thumbnailWidth,
      constants.thumbnailHeight,
      thumbnailCanvas,
      thumbnailWidth / dimensions.width
    );

    // create and update details
    const thumbnailDetails = {
      title: `thumbnail-${collageID}.png`,
      src: '',
      naturalHeight: constants.thumbnailHeight,
      naturalWidth: thumbnailWidth,
      aspectRatio: ar,
      type: 'image/png',
    };
    setThumbnail(thumbnailDetails);
    return [thumbnailDetails, thumbnailCanvas];
  };

  const uploadCollageDetails = (thumbnailDetails, name) => {
    const dets = {
      collage_id: collageID.toString(),
      name,
      collage_type: collageType,
      created_at: '2021-02-18T21:54:42.123Z',
      last_updated_at: '2021-02-18T21:54:45.123Z',
      images: images.getImageData(images.images),
      tree: treeState.tree.removeImageData(),
      thumbnail: thumbnailDetails,
    };
    // TODO [33] Validate collageDetails
    console.log(dets);
    return collageapi.newCollage(collageID, dets);
  };

  const uploadCallback = () => {
    console.log('Setting uploaded images to ', uploadedImages + 1);
    setUploadedImages((cnt) => cnt + 1);
  };

  const uploadCollageImages = (thumbnailCanvas, imageURLS) => {
    const imagePromises = Object.keys(imageURLS).map(async (item) => {
      const thumbnailKey = `thumbnail-${collageID}.jpg`;
      if (item === thumbnailKey) {
        const thumbnailPromise = thumbnailCanvas.toBlob(async (blob) => {
          setThumbnail(blob);
          return collageapi.upload(imageURLS[thumbnailKey], blob);
        });
        return thumbnailPromise;
      }

      const url = imageURLS[item];
      const Img = images.images.find((img) => img.title === item);
      const resp = await collageapi.upload(url, Img.file);
      uploadCallback();
      console.log('image upload resp', resp);
      return resp;
    });

    return imagePromises;
  };

  const newCollage = async (name) => {
    const ar = treeState.tree.root.value.aspectRatio;
    console.log('in generate thumbnail', ar, 500 * ar, 500);
    const [thumbnailDetails, thumbnailCanvas] = generateThumbnail();
    updateStatus('details', 'running');
    const imageURLS = await uploadCollageDetails(thumbnailDetails, name);
    updateStatus('details', 'done');
    updateStatus('images', 'running');
    const imagePromises = uploadCollageImages(thumbnailCanvas, imageURLS);
    const uploadPromises = await Promise.allSettled(imagePromises);
    // updateStatus('images', 'done');
    updateStatus('finalizing', 'running');
    let uploadStatus;
    for (let i = 0; i < uploadPromises.length; i += 1) {
      if (
        uploadPromises[i].status === 'fulfilled' ||
        uploadPromises[i].value.ok === true
      ) {
        uploadStatus = true;
        updateStatus('finalizing', 'done');
      } else {
        uploadStatus = false;
        updateStatus('finalizing', 'failed');
        break;
      }
    }
    return uploadStatus;
  };

  useEffect(() => {
    const { fontSize, lineHeight } = constants;
    if (
      options.options.text.length > 0 &&
      Object.keys(treeState.tree).length > 0
    ) {
      const tH = helpers.wrapText(
        options.options.text,
        0,
        0,
        dimensions.width,
        fontSize,
        lineHeight
      );
      dimensions.setTextHeight(tH.length * lineHeight);
      const [w, h] = helpers.getCollageDimension(
        dimensions.width,
        dimensions.height - tH.length * lineHeight,
        treeState.tree.root.value.aspectRatio
      );
      dimensions.setImageWidth(w);
      dimensions.setImageHeight(h);
    } else {
      dimensions.setTextHeight(0);
      options.handlePositionChange(true);
      dimensions.setImageWidth(dimensions.width);
      dimensions.setImageHeight(dimensions.height);
    }
  }, [
    dimensions.width,
    dimensions.height,
    treeState.tree,
    options.options.text,
  ]);

  const getDetails = (name, thumbnailDetails) => {
    setCollageName(name);
    return {
      collage_id: collageID.toString(),
      name,
      collage_type: collageType,
      created_at: '2021-02-18T21:54:42.123Z',
      last_updated_at: '2021-02-18T21:54:45.123Z',
      images: images.getImageData(images.images),
      tree: treeState.tree.removeImageData(),
      thumbnail: thumbnailDetails,
    };
  };

  const saveCollage = async (name) => {
    const ar = treeState.tree.root.value.aspectRatio;
    console.log('in generate thumbnail', ar, 500 * ar, 500);
    const [thumbnailDetails, thumbnailCanvas] = generateThumbnail();
    updateStatus('details', 'running');
    const imageURLS = await collageapi.saveCollage(
      collageID,
      getDetails(name, thumbnailDetails)
    );
    console.log('save collage imageurls', imageURLS);
    updateStatus('details', 'done');
    updateStatus('images', 'running');
    const imagePromises = uploadCollageImages(thumbnailCanvas, imageURLS);
    const uploadPromises = await Promise.allSettled(imagePromises);
    // updateStatus('images', 'done');
    updateStatus('finalizing', 'running');
    let uploadStatus;
    for (let i = 0; i < uploadPromises.length; i += 1) {
      if (
        uploadPromises[i].status === 'fulfilled' ||
        uploadPromises[i].value.ok === true
      ) {
        uploadStatus = true;
        updateStatus('finalizing', 'done');
      } else {
        uploadStatus = false;
        updateStatus('finalizing', 'failed');
        break;
      }
    }
    return uploadStatus;
  };

  console.log('tree', treeState.tree);
  return [
    collageType,
    getDetails,
    collageName,
    thumbnail,
    collageID,
    constants,
    dimensions,
    options,
    actions,
    images,
    treeState,
    handleClear,
    collageCallback,
    handleRemoveImage,
    newCollage,
    saveStatus,
    saveCollage,
    handleCollageTypeChange,
    handleNameChange,
  ];
}
