import { useState, useEffect } from 'react';
import helpers from 'components/collage/helpers';

export default function useImages(initialValue) {
  console.log('use images', initialValue);
  const [newImgCount, setNewImageCount] = useState(0);
  const [newImagesLoaded, setNewImagesLoaded] = useState(0);
  const [imgCount, setImageCount] = useState(0);
  const [loaded, setLoaded] = useState(0);
  const [images, setImages] = useState([]);

  useEffect(() => {
    console.log('setting images');
    if ('images' in initialValue) {
      setImages(initialValue.images);
      if (imgCount === 0) setImageCount(initialValue.images.length);
    }
  }, [initialValue]);

  const getImageData = (imageArr) => {
    const tempImgArr = [...imageArr];
    return tempImgArr.map((img) => {
      const { file, src, ...rest } = img;
      console.log('get image data', img, rest);
      return rest;
    });
  };

  const removeImageData = (imageArr) => {
    const tempImgArr = [...imageArr];
    return tempImgArr.map((img) => {
      const { file, src, ...rest } = img;
      console.log('get image data', img, rest);
      return rest;
    });
  };

  const incrementNewImageCount = () => {
    console.log('incrementing image count');
    setNewImagesLoaded((cnt) => cnt + 1);
  };

  const handleImageData = async (imageDetail) => {
    setImages((arr) => arr.concat([imageDetail]));
    setNewImagesLoaded((cnt) => cnt + 1);
  };

  const handleLoad = (event) => {
    helpers.handleImageLoad(
      event,
      setImageCount,
      setNewImageCount,
      handleImageData
    );
  };

  const clear = () => {
    setImages([]);
    setImageCount(0);
    setLoaded(0);
    setNewImagesLoaded(0);
  };

  const remove = (title) => {
    const removeIndex = images.findIndex((elem) => elem.title === title);
    const newImagesArr = [
      ...images.slice(0, removeIndex),
      ...images.slice(removeIndex + 1),
    ];
    setImages(newImagesArr);
    setImageCount((cnt) => cnt - 1);
    return newImagesArr;
  };

  return {
    clear,
    handleImageData,
    handleLoad,
    images,
    imgCount,
    loaded,
    newImagesLoaded,
    newImgCount,
    remove,
    setImageCount,
    setImages,
    setLoaded,
    setNewImageCount,
    setNewImagesLoaded,
    getImageData,
    removeImageData,
    incrementNewImageCount,
  };
}
