import Typography from '@mui/material/Typography';
import { alpha } from '@mui/material/styles';

export default function TextDisplay(textArray, position) {
  return (
    <>
      {
        // eslint-disable-next-line react/destructuring-assignment
        textArray.map((chunk) => (
          <Typography
            key={chunk[2]}
            sx={{
              width: '100%',
              position: 'absolute',
              top: position + chunk[2],
              bgcolor: alpha('#1c1c1c', 0.7),
              paddingX: 1,
              color: 'button.main',
              textAlign: 'center',
              fontFamily: 'Arial',
              fontSize: '20px',
              lineHeight: '25px',
              letterSpacing: 'normal',
            }}
          >
            {chunk[0]}
          </Typography>
        ))
      }
    </>
  );
}
